.skills-wrapper{
  display: grid;
  margin: 20px;
  margin-top: 0;
  max-width: 900px;
  grid-template-columns: 1fr 1fr;
}

.centered-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (max-width: 400px) {
  .skills-wrapper{
    display: flex;
    flex-direction: column;
    margin: 20px;
    margin-top: 0;
    max-width: 900px;

  }
}